.cardContainer {
  position: relative;
  width: 100px;
  height: 150px;
  border: solid 1px red;
  margin: 10px;
}

.cardImage {
  object-fit: cover;
  width: 100px;
  height: 150px;
}

.skeleton {
  width: 100px;
  height: 125px;
}

.closedImage {
  color: red;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: x-large;
}
